<template>
  <div>
    <el-form ref="form" size="small" :model="form" label-width="100px">
      <el-form-item label="打印选择">
        <el-radio v-model="form.type" :label="0">二四特惠</el-radio>
        <el-radio v-model="form.type" :label="1">二四优递</el-radio>
      </el-form-item>

      <el-form-item v-if="form.type === 0">
        <el-form-item label="商品名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <span>{{ form.phone }}</span>
        </el-form-item>
        <el-form-item label="商品规格">
          <el-input v-model="form.sku"></el-input>
        </el-form-item>
        <el-form-item label="产地">
          <el-input v-model="form.address"></el-input>
        </el-form-item>
        <el-form-item label="生产日期">
          <el-date-picker
            v-model="date"
            type="date"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="储存条件">
          <el-input v-model="form.conditions"></el-input>
        </el-form-item>
        <el-form-item label="打印份数">
          <el-input v-model.number="form.number" style="width: 200px">
            <template slot="prepend">×</template>
          </el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item v-else>
        <el-form-item label="订单编号">
          <el-input v-model="form.logistcsNumber"></el-input>
        </el-form-item>
        <el-form-item label="收件人名称">
          <el-input v-model="form.receiverName"></el-input>
        </el-form-item>
        <el-form-item label="收件人手机号">
          <el-input v-model="form.receiverPhone"></el-input>
        </el-form-item>
        <el-form-item label="收件人地址">
          <el-input v-model="form.receiverAddress"></el-input>
        </el-form-item>
        <el-form-item label="寄件人姓名">
          <el-input v-model="form.sellerName"></el-input>
        </el-form-item>
        <el-form-item label="寄件人电话">
          <el-input v-model="form.sellerPhone"></el-input>
        </el-form-item>
        <el-form-item label="寄件人地址">
          <el-input v-model="form.sellerAddress"></el-input>
        </el-form-item>
        <el-form-item label="发货时间">
          <el-date-picker
            v-model="createTime"
            type="date"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="form.orderRemarks"></el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item>
        <el-button @click="onLodop">打印</el-button>
      </el-form-item>
    </el-form>
    
    <lodop-cmp ref="lodop" />
  </div>
</template>

<script>
import lodopCmp from '@/components/lodop_cmp/index.vue'

export default {
  components: {
    lodopCmp,
  },

  data () {
    return {
      form: {
        // 二四特惠
        type: 0, // 选择打印方式
        name: '', // 商品名称
        phone: '0512-63332424', // 电话
        sku: '', // 商品规格
        date: '', // 生产时间
        address: '', // 产地
        conditions: '', // 储存条件
        number: '', // 打印数量

        // 二四优递
        logistcsNumber: '', // 订单编号
        receiverName: '', // 收件人名称
        receiverPhone: '', // 收件人手机号
        receiverAddress: '', // 收件人地址
        sellerName: '', // 寄件人姓名
        sellerPhone: '', // 寄件人电话
        sellerAddress: '', // 寄件人地址
        createTime: '', // 发货时间
        orderRemarks: '', // 备注
      },
      date: '',
      createTime: '',
    }
  },

  watch: {
    date (newVal) {
      const DATE = new Date(newVal)
      const Year = DATE.getFullYear()
      const Month = DATE.getMonth() + 1
      const Day = DATE.getDate()
      this.form.date = `${Year}.${Month}.${Day}`
      console.log('this.form.date', this.form.date)
    },
    createTime (newVal) {
      const DATE = new Date(newVal)
      const Year = DATE.getFullYear()
      const Month = DATE.getMonth() + 1
      const Day = DATE.getDate()
      this.form.createTime = `${Year}.${Month}.${Day}`
      console.log('this.form.createTime', this.form.createTime)
    }
  },

  created () {
    if (JSON.parse(localStorage.getItem('lodopForm'))) {
      this.form = JSON.parse(localStorage.getItem('lodopForm'))
      this.date = new Date(this.form.date)
    }
    this.createTime = new Date()
  },

  methods: {
    onLodop () {
      localStorage.setItem('lodopForm', JSON.stringify(this.form))
      switch (this.form.type) {
        case 0:
          for (let i = 0; i < this.form.number; i++) {
            this.$refs.lodop.printWohoTeHui(this.form)
          }
          break
        case 1:
          this.$refs.lodop.printCode(this.form)
          break
        default:
          break
      }
    },
  }
}
</script>

